.task-form{
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
}

.task-input{
    width: 100%;
    font-size: 18px;
    padding: 14px 32px 14px 16px;
    border-radius: 4px 0 0 4px;
    border: 2px solid #006a29;
    outline: none;
    margin: 5px 0;
    background-color: #0a0a24;
    color: white;
}
.task-input:hover{
    background-color: #121243;
}
.task-input::placeholder{
    color: white;
    opacity: 0.7;
}
.task-input:focus::placeholder{
    opacity: 0;
}

.task-btn{
    padding: 16px;
    font-size: 18px;
    border: none;
    border-radius: 0px 4px 4px 0;
    cursor: pointer;
    outline: none;
    background-color: #006a29;
    color: #fff;
    text-transform: capitalize;
    min-width: 110px;
}

.task-btn:hover{
    background-color: #009639;
}
.task-btn:active{
    background-color: #00ba47;
}
