*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,body{
  color: #fff;
  background-color: #1b1b32;
  font-family: monospace, sans-serif;
  margin-top: 40px;
}

.tasks-app{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

/* -----------------------------Tasks main list */
.tasks-main-list{
  width: 600px;
  min-height: 500px;
  background-color: #d0d0d5;
  padding: 25px;
  margin: 10px;
  border-radius: 25px;
  font-size: calc(4px + 2vmin);

}
.h1{
  color: #1b1b32;
  font-family: sans-serif;
  text-align: center;
  margin: 20px 0;
}